.plasticYear {
    margin-top: 0px;
    height: 100vh;
    color: white;
    text-align: center;
    background: white;
    
   
}

h1 {

    padding-top: 50px;
    font-size: 25px;
    color: white;
}


.plastic_page {
  text-align: center !important;
 
}

.component {
    max-height: 700px;
    max-width: 650px;
    text-align: center;
    color: white;

    border-radius: 20px;
    background: #15481E;
    padding: 20px;

  
}



.component input {
 background: #15481E;
 
 max-width: 54px;
 margin: 9px;
 
 border: 1px solid white;
 outline: none;
 border-radius: 10px;
 font-size: 30px;
 line-height: 30px;
 text-align: center;
 color: white;
padding-bottom: 8px;
}




.component a {
    color:  white;
}

.component h1 {
    color:white;
    font-weight: 600;
}



.recycled {
    display: grid;
    grid-template-columns: 1fr 20px 1fr;
}

.recycled img {
    max-height: 150px;
}

.noPlastic {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px
}

.link {
    text-align: center;
    background: transparent;
    color: white;
    font-size: 27px;
    border-radius: 15px;
    border: 1px solid white;
    width: auto;
    line-height: 27px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 5px;
}



.link:hover {
    color: white;
    background: #15481E;
}

a:hover {
    color: white;
}

.info p {
    font-size: 18px;
}




.pictures img {
    max-width: 230px;
    border: 1px solid white;
    border-radius: 10px;
}

.picture_hm img {
    width: 100% !important;
}


.pictures {
    display: grid;
    grid-template-columns: 1fr 1fr;
}



h5 {
    font-weight: 600;
}

.arrow a{
    color: white;
    font-size: 35px;
    font-weight: 300;
    position: absolute;
    
   
}

.arrow {
    position: absolute;
    top: 50%;
    right: -3%;
}

.companies {
    position: relative;
}

.component_companies {
   max-height: 300px;
  max-width: 600px;
    text-align: center;
    color: white;
margin: 25px;
    border-radius: 20px;
    background: #15481E;
    padding: 20px;
}

.company p {
    font-size: 16px;
    font-family: Georgia;
    color: black;
}

.company {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    align-items: center;
    justify-content: center;
    justify-items: center;
    text-align: center;
}

.company_hm {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.company_text {
   
    max-height: 300px;
    padding: 10px;
    margin-top: 5px;
    color: black;
    background: white;
    border: 1px solid white;
    border-radius: 20px;
    -webkit-box-shadow: 10px 10px 20px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 20px -4px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 20px -4px rgba(0,0,0,0.75);

}

.company_text p {
    margin: 0;
}



.round {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    border: 1px solid white;
}
