



p {
    font-size: 20px;
    color: white;
}

.componentR {
    max-height: 400px;
    width: auto;
    padding: 20px;
    border-radius: 20px;
    background: #15481E;
}

.componentR h2 {
    color: white;
    font-size: 30px;
    font-family: 'Playfair Display', serif;
    text-align: center;
}

.recycled img {
    
    max-height: 150px;
}


.padding-top p {
    color: white;
}

.recycle {
    font-size: 20px;
    color: white;
}

.recycled {
    display: grid;
    grid-template-columns: 1fr 0.5fr 1fr 0.5fr 1fr;
    margin: 10px;
    justify-items: center;
    justify-self: center;
    text-align: center;
}

.link {
    margin-top: 30px;
    text-align: center;
    background: #15481E;
    color: white;
  
}

a {
    font-size: 20px;
    color: white;
}

.link:hover {
    color: white;
}

.recycle_page {
    margin-bottom: 30px;
}