body,
html,
#root {
  height: 100%;
  text-align: center;
 
}

body {
  overflow-y: scroll;
  display: grid;
  justify-items: center;
 
 
}

html {
  font-size: 18px;
 

}


.App {
  

  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  color: #fff;
  padding-top: 50px;
  background-image: url("./background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
 
  
}



.select {
  color: black;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding-bottom: 10px;
  
}

@media only screen and (max-width: 700px) {

.container {
  width: 280px;
  
}
  
.select {
  font-size: 10px;
}
h1 {
  font-size: 23px;
}
h2 {
  font-size: 17px;
}

}





