@media (max-width: 680px) {

.last_page {
    width: 300px !important;
    margin: 50px;
}

.plastic_year_wrapper, .recycle_page {
    width: 300px;
}

.component h1, .componentR h2 {
    font-size: 20px !important;
}

.component input {
    width: 30px;
    height: 30px;
    font-size: 20px;
}

.info img{
    width: 150px;
    height: auto !important;
}

.info {
    grid-template-columns: 1fr !important;
}

.recycled img {
    width: 70px;
}

.recycled {
    grid-template-columns: 1fr 0px 1fr  !important;
}

.or, .produce {
    display: none;
}

.componentR p {
    font-size: 18px !important;
}


}


.plastic_wrapper {
    background: white;
    border-radius: 14px;
    position: relative;
    max-width: 400px;
    min-height: 400px;
    margin: 20px;
}

.noPlasticWrapper {
   
    font-size: 40px;
    text-align: center;
   background: white;
   border-radius: 30px;
 

}

.noPlasticPage {
    position: relative;
}

.noPlastic h1 {
    margin: 0 !important;
}

h2 {
    color: #045C6B;
    font-weight: 600;
    padding-top: 20px;
    font-family: 'Playfair Display', serif;
    
}

.plasticProducts {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    grid-gap: 10px;
    padding-bottom: 50px;
    
}

img {
    max-width: 250px;
    max-height: 250px;
    margin: 20px;

}


.active {
   
    display: block;
}

.plastic_item {
min-height: 400px;
 display: grid;
 align-items: center;
 justify-items: center;
 cursor: pointer;

}

.plastic_item p {
   padding: 10px;
    width: 90%;
    font-size: 15px;
    color: #2A202A;
    margin: 0px 15px 15px 15px;
}



.arrow {
   
    position: absolute;
    top: 40% !important;
    left: 75%;
}

.arrow a {
    color: #2A202A !important;
}

.last_page {
    background: white;
    border-radius: 14px;
    position: relative;
    width: 400px;
    height: 450px;
    display: grid;
    align-items: center;
    margin: 30px;
}