.water img {
    width: 250px;
    height: auto;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    
}

.water p {
    color: white;
    position: absolute;
    top: 480px;
    font-size: 20px;
    font-weight: 600;
    width: 250px;
    text-align: center;
}


.food {
display: grid;
grid-template-columns:  1fr 1fr;
align-items: center;
justify-items: center;


}

.container {
    background-color: #EEF6CB;
    border-radius: 25px;
    border: 2px solid #73AD21; 
    text-align: center;
    max-height: 600px;
  }


.product img {
   width: 250px;
   height: auto;
}

@media only screen and (max-width: 700px) {
    .food {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        justify-items: center;
        }
    .product img {
        width: 80px;
    }
  
    .water img {
        width: 150px;
    }
    .water p {
        width: 150px;
        font-size: 14px;
        top: 385px;
        color: white;
    }
}