.info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 15px;
}


.info img {
    height: 150px;
    box-shadow: none !important;
}

.noPlastic {
   
    font-size: 40px;
    text-align: center;
    max-height: 400px;
    width: 100%;
    border-radius: 20px;
    background: white;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
}


.noPlastic p {
    font-size: 26px;
    color: white;
}

.noPlastic h1 {
    color: #15481E;
    font-weight: 600;
    padding: 10px;
    font-family: 'Playfair Display', serif;

}



.padding-top a {
    font-size: 24px;
   font-weight: 600;
    padding: 20px;
}


.red {
    font-size: 50px;
    color: red;

}

.noPlastic img {
    height: 250px;
   
    box-shadow: none !important;
}

.link_hero {
    margin: 0;
}